
$navbar-bg: #8ACFC5;
$textcolor: black;
$navbar-font-color: white;
$operating-btn-color:rgb(214, 39, 230);
$navbar-bghover:#83c7c7;

@import url('https://fonts.googleapis.com/css?family=Poppins:400,500,700');

/* GLOBAL STARTS */



html, body {
    height: 100%;
    width: 100%;
    margin: 0%;
    font-family: 'Poppins', sans-serif;
	color: $textcolor;
}


.centered{
  text-align: center;
}

.padding {
    padding-bottom: 2rem;
}

.separator{
  border-top: 1px solid #e1e1e1;
  width: 75%;
  margin-top: 0.8rem;
  margin-bottom: 1rem;
}

.vl {
  border-left: 1px solid #e1e1e1;
  height: 100%;
  margin: 10%;
}

/* GLOBAL ENDS */

/* NAVBAR STARTS */

.navbar{
  background-color: $navbar-bg ;
  padding: 0.8rem;
}

.navbar-collapse{
  text-align: center;
}

.navbar-nav li {
    padding-right: 20px;
}

.nav-link {
    font-size: 2rem;
    color: $navbar-font-color ;
}

.nav-link:hover{
  color:grey ;
  text-shadow: $navbar-font-color ;
}

.nav-link:active{
  font-size: 2rem;
  text-decoration: underline;
}

.dropdown-menu{
  background-color: $navbar-bg;
  width:100%;
  text-align: center;
}

.dropdown-item{
  color:$navbar-font-color;
  font-size: 1.5rem;
}

.dropdown-item:hover{
  background-color: $navbar-font-color;
  color: $navbar-bghover;
  font-size: 1.5rem;
}



/* NAVBAR ENDS */

/* BREADCRUMBS START */

.breadcrumb{
  background-color: #343d4c;
  border-radius: 0;
}

.breadcrumb-index{
  background-color: #343d4c;
  border-radius: 0;
  margin-bottom: 0;
}

.breadcrumb-item a{
  color: $navbar-font-color;
}

/* BREADCRUMBS END */

/* HERO IMAGE STARTS */


.hero-text a {
    font-size: 1.3rem;
    border: none;
    outline: 0;
    display: inline-block;
    padding: 0.5rem 1.25rem;
    color: $navbar-font-color;
    background-color: $navbar-bg;
    text-align: center;
    cursor: pointer;
}

img.img-responsive{
	width: 350;
}


.btn-primary{
  background-color: $operating-btn-color;
  border: $operating-btn-color;
}

.hero-image {
  background-image: url("image/bg01.png");
  height: 70%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.hero-text {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: $navbar-font-color;
}

.hero-text h1 {
    font-size: 4.5rem;
    text-transform: uppercase;
    text-shadow: 1px 1px 15px #000;
}
.hero-text h3 {
    font-size: 2rem;
    font-weight:500;
    text-shadow: 1px 1px 10px #000;
    padding-bottom: 1rem;
}

.hero-text a {
  font-size: 1.3rem;
  border: none;
  outline: 0;
  display: inline-block;
  padding: 0.5rem 1.25rem;
  color: $navbar-font-color;
  background-color: $navbar-bg;
  text-align: center;
  cursor: pointer;
}

/* HERO IMAGE ENDS */

/* ICONS START */

.fa-bolt{
  color: #563d7c;
}

.fa-shield-alt{
  color: #e54d26;
}

.fa-globe{
  color: #2163af;
}

.fa-bolt, .fa-shield-alt, .fa-globe{
   font-size: 4em;
   margin: 1rem;
}

h3{
  font-size: 2.5rem;
}

/* ICONS END */

/* PARALLAX IMAGE STARTS */
figure {
  position: relative;
  width: 100%;
  height: 60%;
  margin: 0!important;
}
.fixed-wrap {
  clip: rect(0, auto, auto, 0);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
/* #fixed {
  background-image: url('image/bg02.jpg');
  position: fixed;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center center;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
  will-change: transform;
} */
/* PARALLAX IMAGE ENDS */

/* MISSION STARTS */

.mission{
  text-align: center;
}

.mission-text{
  margin-top: auto;
}

.mission img{
  border-color: #555555;
  border-width: 1.5rem;
  border-style: solid;
}

/* MISSION ENDS */

/* FOOTER STARTS */

footer {
  background-color: $navbar-bg;
  
}

.social a {
  font-size: 3em;
  padding: 1.2rem;
}

.fa-facebook:hover {
    color: #3b5998;
}
.fa-twitter:hover {
    color: #00aced;
}
.fa-google-plus-g:hover {
    color: #dd4b39;
}
.fa-instagram:hover {
    color: #fb3958;
}
.fa-youtube:hover {
    color: #bb0000;
}
.fa-facebook,
.fa-twitter,
.fa-google-plus-g,
.fa-instagram,
.fa-youtube {
    color: #d5d5d5;
}

.copyright{
  text-align: center;
  color:$navbar-font-color;
}

.copyright h5{
  font-size: 2rem;
}

/* FOOTER ENDS */

/* BOOK.PHP STARTS */

.phonebook{
  background-color: $navbar-bg;
  width: 100rem;
  color: $navbar-font-color;
  text-align: center;
}

.phonebook h4{
  color: $textcolor;
  font-size: 2rem;
}

.phonebook-container{
  text-align: center;
  margin-left: auto;
  margin-right: auto;
}

.phonebook hr{
  border-color: $navbar-font-color;
  width: 70%;
}

.pagination{
  font-size: 1rem;
  
}

.page-item{
  width: 4rem;
  height: 2rem;
  font-size: 1rem;
  /* background-color: #ffffff;
  color: rgb(0, 0, 0); */
  border-radius: 0.8rem;
  /* box-shadow: inset 0 0.64rem 1rem rgba(0,0,0,.1) 0 0.2rem rgba(0,0,0,.5); */
}




.page-item.active{
  background-color: #6390cc;
  color: rgb(0, 0, 0);
}

.page-item.active:hover{
  background-color: #83c7c7;
  color: $navbar-font-color;
}

.page-item.disabled{
  background-color: #6e7277;
  color: rgb(255, 255, 255);
}

.page-item.disabled:hover{
  background-color:  #6e7277;
  color: rgb(255, 255, 255);
  /* cursor:default; */
}

.page-item:hover{
  background-color: #83c7c7;
  color: $navbar-font-color;
}


table{
  width: 95%;
  margin: 0% 2.5%;
}

table tr{
  background-color: $navbar-bg;
}

table td{
  padding-top: 1.2rem;
  padding-bottom: 1.2rem;
}

.td-img{
  padding-left: 1.2rem;
  padding-right: 0rem;
  border-top-left-radius: 1rem;
  border-bottom-left-radius: 1rem;
}

.thumbnail{
  width: 12rem;
  height: 15rem;
  border-width: 0.5rem;
  border-style: ridge;
  border-radius: 5%;
  border-color: $navbar-font-color;
}

.td-content{
  padding-left:1rem;
  padding-right:1rem;
  width:100%;
  padding-top: 3.5%;
  padding-bottom: 2%;
}

table .data-header{
  background-color: $navbar-font-color;
  width: 100%;
  padding:0%;
}

table h4{
  color: $textcolor;
  font-size: 2.5rem;
  font-style: italic;
}

h4 .occupation{
  font-size:1.2rem;
}

table p{
  color: $navbar-font-color;
  font-size: 1.6rem;
  font-weight: bold;
}

table .content-head{
  color: #6390cc;
  font-size: 1.7rem;
}

table .td-button{
  padding-right: 1.2rem;
  border-top-right-radius: 1rem;
  border-bottom-right-radius: 1rem;
}

.addbtn{
  width:8rem;
  margin: 1.2rem 0rem;
}

.view{
  width:8rem;
  margin: 1.2rem 0rem;
  cursor: pointer;
  color: $navbar-font-color;
}

.update{
  width:8rem;
  margin: 1.2rem 0rem;
  cursor: pointer;
  color: $navbar-font-color;
}

.delete{
  width:8rem;
  margin: 1.2rem 0rem;
  cursor: pointer;
  color: $navbar-font-color;
}

.modal-header{
  background-color:$navbar-bg ;
}

.modal-title{
  color: $navbar-font-color ;
}

.close-btn{
  color: $navbar-font-color ;
}

/* CARDS START */
.card{
  width: 90%;
  background-color: $navbar-bg;
}
.card-img-top{
  width: 75%;
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.card h4{
  background-color: gold;
  color: $textcolor;
}
.card p{
  color:$textcolor;
}
.card .content-head{
  color: #6390cc;
}
/* CARDS END */

/* BOOK.PHP ENDS */

/* MEDIA QUERIES START */

@media (min-width: 992px) {
    img.img-responsive{
	    width: 350;
    }
}

@media (max-width: 992px) {
  .social a {
    font-size: 3em;
    padding: 1.2rem;
  }
  .copyright h5{
    font-size: 2rem;
  }
  .navbar-brand #logo{
	
    height: 5rem;
  }
}

@media (max-width: 820px) {
  /* Force table to not be like tables anymore */
  table, thead, tbody, tr, td {
    display: block;
    padding: 0;
    text-align: center;
  }

  tr {
      margin: 0 0 1rem 0;
    }

  td {
    /* Behave  like a "row" */
    border: none;
    border-bottom: 1px solid #eee;
    position: relative;
  }

  .td-img{
    text-align: center;
  }

  .td-button{
    text-align: left;
    display: flex;
    padding: auto;
    width: auto;
  }

  table .btn{
    margin: 0 auto;
  }
}
.navbar-brand #logo{
	
  height: 5rem;
}

@media (max-width: 768px) {
  .hero-text h1{
    font-size: 4rem;
  }

  .hero-text h3{
    font-size: 2rem;
  }

  .hero-text button {
    font-size: 1rem;
    padding: 7px 18px;
  }
  /* .navbar-brand img{
    max-width: 12rem;
  } */

  .navbar-brand #logo{
	
    height: 5rem;
  }

  .social a {
    font-size: 2.5em;
    padding: 0.9rem;
  }
  .copyright h5{
    font-size: 1.5rem;
  }
}

@media (max-width: 576px) {
  .hero-text h1{
    font-size: 3.5rem;
  }

  .hero-text h3{
    font-size: 1.7rem;
  }

  .hero-text button {
    font-size: 1rem;
    padding: 7px 18px;
  }
  /* .navbar-brand img{
    max-width: 9rem;
  } */


  .navbar-brand #logo{
	
    height: 5rem;
  }

  .social a {
    font-size: 2em;
    padding: 0.7rem;
  }

  .copyright h5{
    font-size: 1.2rem;
  }
}



/* MEDIA QUERIES END */

